.documentation {
    .doc-sidebar {
        position: relative;
        padding-top: 30px;
        .search-container {
            position: fixed;
            top: 0px;
            width: 248px;

            .clear-btn {
                position: absolute;
                right: 8px;
                top: 1px;
                font-size: 12pt;
                color: white;
                cursor: pointer;
            }

            img {
                position: absolute;
                top: 8px;
                left: 5px;
                width: 15px;
                height: 15px;
            }

            input {
                width: 100%;
                background-color: black;
                color: white;
                border-radius: 8px;
                padding-left: 25px;
            }
        }
    }
}
