@media screen and (max-width: 600px) {

    .crypto-row {
        flex-direction: column;
    }

    .golden.initial {
        margin-left: 0px !important;
        width: 100vw !important;
        height: 68vw !important;


        .nft, .splits {
            padding-top: unset !important;
            padding-left: unset !important;
            font-size: 15pt !important;
            line-height: unset !important;

            .txt {
                margin-left: 20px;
            }

            span {
                margin-left: auto;
                margin-right: auto;
            }
        }

        .on-chain {
            padding: 8pt !important;
            font-size: 10pt !important;
        }

        .open-source {
            position:relative;
            font-size: 15pt !important;
            .coming-soon-open {
                position: absolute;
            }

        }

        .coming-soon {
            font-size: 6pt !important;
            width: 100% !important;
            bottom: 3px !important;
            left: 0px !important;
        }
    }
    
    .diagrams {
        margin-left: auto !important;
        margin-right: auto !important;
        margin-top: 6.06vw !important;
    }
    
    .more-crypto {
        margin-left: 3.03vw !important;
        font-size: 14pt !important;
    }
}

.golden {
    display: flex;
    width: 100%;

    .golden.golden-row {
        height: 61.803%;
    }

    .golden.golden-column {
        width: 61.803%;
    }
}

.golden.initial {
    margin-left: 8.08vw;
    width: 600px;
    height: 370.820px;
    border-top: 1px dashed black;
    border-left: 1px dashed black;
}

.golden.golden-row {
    flex-direction: row;
    .golden-element {
        width: 100%;
        height: 100%;
    }
}

.golden {
    .golden-element {
        border-bottom: 1px dashed black;
        border-right: 1px dashed black;
    }
}
.golden {
    div:nth-child(n+2) {
        //margin-left: -1px;
    }
}
.golden.golden-column {
    flex-direction: column;
    .golden-element {
        height: 100%;
        width: 100%;
    }
}
