
.landing-page.showing-doc {
    overflow: hidden;
}
.documentation {
    min-height: 100vh;

    ::-webkit-scrollbar-thumb {
        background: #4b4b4b61 !important;
    }

    .logo-btn {
        cursor: pointer;
        position: absolute;
        top: 20px;
        right: 30px;
        width: 80px;
    }


    display: flex;
    //padding-top: 11.1vw;;
    //min-height: 1000px;
    background-color: #212123c4;
    //background-color: #e2887cb8;
    color: white;
    overflow: hidden;
    font-family: 'Univers Condensed';

    svg {
        circle {
            cursor: pointer;
        }
    }

    .coke-machine-glow {
        opacity: 0.05 !important;
    }

    .presets-explorer-container {
        //display: flex;
//        max-height: 270px;
        //overflow: hidden;
    }

    .two-column {
        display: flex;

        .text-column {
            margin-right: 15px;
            width: 50%;

            .effect {
                max-width: 250px;
                float: none;
                margin: 0px;
                margin-top: 30px;
            }
        }
    }

    a, .doc-link {
        color: #218dff !important;
    }

    .mini-sequencer {
        margin-top: 10px;
    }
    
    .getting-started-screenshot {
        width: 100%;
        height: 100%;
    }

    .track.lfos-track, .track.selected, .track.odd.selected {
        opacity: 1;
    }

    .track, .track.odd {
        .track-header {
            .track-name-view {
                color: white;
            }
        }
    }

    .track-quick-view {
        background-color: #bfbebec4;;
        
        .sample-arrows, .parameter-option.selected, .Dropdown-control, .name-label, .parameter-value {
            background-color: #ffffff !important;
            color: black !important;
            
            .left, .right {
                border-color: black;
            }
        }

        .sample-arrows:active {
            box-shadow: 0px 0px 1px 1px white;
        }

        .parameter-option {
            color: white;
        }
    }
 
    .interactive-view {
        display: flex;
        
        .hide.sounds-pane, .sounds-pane {
            height: 200px !important;
       }

        
        .sounds-pane {
            margin-left: 5px;
            color: white;

            .samples-stream-explorer {
                height: 140px;
            }
        }
    }

    .doc-link:hover {
        color: #5f9fff;
    }

    .doc-link {
        color: #357cff;
        text-decoration: underline;
        cursor: pointer;
    }
    
    #track-details-documentation {
        max-width: 700px;
    }

    .track.selected {
        color: white;
    }

    .doc-sidebar {
        width: 250px;
        height: 100%;;
        position: fixed;
        border-right: 2px solid gray;
        overflow-y: scroll;
        // background-color: #6363ea24;
        // padding-bottom: 150px;

        .topic {
            font-size: 14pt;
            padding: 5px;
            padding-left: 20px;
            cursor: pointer;
            color: gray;
        }

        .topic:hover {
            color: white;
        }

        .topic.selected {
            color: white;
        }
    }

    .tracks-container {
        max-width: 100%;
    }

    .doc-viewer.wide {
        max-width: 1200px;
    }

    .doc-viewer {
        flex: 1;
        padding-left: 20px;
        padding-right: 15px;
        padding-top: 20px;
        max-width: 1200px;
        margin-left: 260px;
        height: 100%;

        p {
            margin-top: 10px;
            margin-bottom: 10px;
            max-width: 650px;
        }

        .sub-header {
            font-size: 18pt;
            color: #eceaea;
        }

        .header {
            font-size: 48px;
            padding-bottom: 15px;
            border-bottom: 1px solid #353535;
            margin-bottom: 15px;
            color: #eceaea;
        }

        .shortcuts {
            b {
                font-size: 14pt;
            }
        }
    }

    .track-documentation {
        display: flex;
        flex-direction: column;

        .lfos-track {
            display: none;
        }
    }

    .lfos-track {
        .youtube-container {
            background-image: unset !important;
            background-color: #00000024 !important;
        }

        .envelope-component, .lfo-component {
            background-image: unset !important;
            background-color: #00000024 !important;
            color: black;
        }
    }

    .toolbar {
        padding-top: 0px !important;
    }

    .play-container {
        display: flex;
        background-color: #00000024 !important;
        border: 2px solid #1e1e1e;

        .contextual-help {
            margin-left: 10px;
            height: 50px;
            color: white;
            width: 100%;
            flex: 1;
            background-color: #00000024 !important;

            .doc-line.rest {
                color: white;
            }
        }

    }

    .resolution-container {
        margin-left: auto;
        font-size: 9pt;
        margin-bottom: 20px;
    }

    .parent-tag, .tag {
        background-color: #0a0a0a36;
    }

    .selector-container {
        color: white;
        margin-top: 20px;
        margin-bottom: 10px;
        border: solid 1px #072535;
        width: 500px;

        .Dropdown-control {
            background-color: white !important;
            color: black;
        }

        .playlist-row.odd {
            background-color: #0a0a0a36;
        }

        .field-pagination-button {
            background-color: #0a0a0a36;
        }

        #search-input {
            background-color: white !important;
            color: black;
        }
    }

    .effect {
        color: white;
    }

    .track.sends {
        background-color: #0a0a0a36 !important;
        color: white;
        opacity: 1;
    }

    .sequencer-buttons-container {
        background-color: #0a0a0a36;
    }

    .patterns-container, .sub-sequences-container  {
        background-color: #0a0a0a36;
        border: 2px solid #1d1e21;
        margin-left: 0px;
    }        

    .demo-effect-container {
        margin-top: 10px;
    }

    .presets-demo-container {
        width: 80%;
    }

    .groups-component {
        margin-top: 15px;
    }

    .sample-source-demo {
        padding-left: 10px;
        padding-right: 10px;
        padding-top: 10px;
        padding-bottom: 2px;
        background-color: #040512;
        margin-top: 10px;
        border: solid 1px #464d61;
        display: flex;
        width: 400px;
    }

    .piano-roll-container {
        width: 900px;
        .note-blank-side {
            //background-color: #2122213d;
        }

        .piano-roll-row {
            background-color: white;
        }
        .piano-roll-row.black {
            background-color: #e4e4e4;
        }
    }

    .click-effect {
        margin-top: 10px;
        width: 300px;
        height: 200px;
        background-color: #be8cb8;
        display: flex;
        cursor: pointer;

        div {
            margin-top: auto;
            margin-left: auto;
            margin-right: auto;
            margin-bottom: auto;
        }
    }

    .synth-documentation {
        .instrument {
            float: none;
            margin: 0px;
            color: white;
        }

        .envelope-option.selected {
            background-color: white;
            color: black;
        }

        .filter-section, .fm-operator-control, .envelopes-component {
            background-color: #777c845c;
        }

        .fm-operator-demo, .filter-section {
            margin-top: 30px;
        }
        
        .fm-operator-control {
            .Dropdown-control {
                background-color: white !important;
                color: black !important;
            }
 
        }

        .rack-demo {
            .effect-rack {
                margin-top: 30px;
                background-color: #777c845c;
                padding: 5px;

                .algo-container {
                    display: flex;

                    .Dropdown-control {
                        margin-left: 30px;
                        background-color: white !important;
                        color: black !important;
                    }
                }
                
                .rack-toolbar {
                    display: none;
                }

                .horizontal {
                    flex-direction: column;
                }

                .instrument.rack-synth .harmonicity-mod text {
                    fill: white;
                }
            }
        }

        .sample-explorer {
            margin-top: 20px;
            background-color: #777c845c;
            padding: 5px;
            
            .presets-list {
                height: 200px;
            }
        }
    }

    .effect-rack-documentation {
        .effect {
            float: none;
            margin-left: 0px;
        }
    }

    .sampled-instrument-documentation {
        .instrument {
            float: none;
            margin-left: 0px;
        }

        .adsr-demo {
            margin-top: 30px;

            svg {
                background-color: #0d56e2a6 !important;
            }
        }
        
        .samples-to-drag {
            margin-top: 30px;
            width: 300px;
            height: 200px;
            background-color: #be8cb8;
            color: black;
            display: flex;
            flex-wrap: wrap;
            padding: 20px;

            .sample-option {
                width: 50px;
                height: 50px;
                margin-right: 10px;
                margin-bottom: 10px;
                cursor: pointer;
            }

            span {
                margin: auto;
            }
        }
    }

    .getting-started, .track-documentation, .effects-documentation {
        .track.sends, .track.lfos-track {
        //    display: none;
        }
    }
}


.try-it-now {
    a {
        color: white;
    }
}

.documentation {
    .midi-effects {
        display: flex;

        .mini-sequencer {
            display: flex;

            .play-container {
                order: -1;

                .contextual-help {
                    display: none;
                }
            }
        }

        .effect.selected {
            box-shadow: unset;
        }

        .effect {
            margin: 0px;
        }

        .track.lfos-track, .sub-sequence-control {
            display: none;
        }

        .track {
            border: none;
            .track-inner {
                padding: 7px;
            }
            .track-header, .youtube-container, .track-controls, .track-side-container, .track-footer-container {
                display: none;
            }
        }

        .definition {
            padding: 10px;
        }

        .effects-sidebar {
            width: 180px;
            color: white;
            height: 100%;
            border-right: 2px solid gray;
            left: 250px;
            top: 0px;
            overflow: scroll;
            position: fixed;

            .sidebar-divider {
                padding-left: 5px;
                letter-spacing: 4px;
                background-color: #101010;
            }

            .option {
                cursor: pointer;
                padding: 5px;
                padding-left: 15px;
            }

            .option.selected {
                background-color: white;
                color: black;
            }
        }

        .effect-doc {
            flex: 1;
            padding-left: 10px;

            .extra-doc {
                width: 80%;
                margin-top: 15px;
                padding-bottom: 220px;
            }

            .racked.effect {
                .rack-toolbar, .effect-header {
                    display: none !important;
                }
                background-color: unset !important;
            }

            .effect-and-params {
                display: flex;
                margin-top: 20px;
                flex-wrap: wrap;
                
                .params {
                    width: 400px;
                    padding: 10px;
                    margin-left: 20px;
                    background-color: #27273047;
                }
            }
        }
        
    }
}
