.landing-page {
    display: flex;
    flex-direction: column;
    color: #b4b4b4;
    font-family: Inter, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";

    .navigator {
        background: linear-gradient(90deg, #a23388 1%, #ebae8b 85%);
        color: white;

        .logo-pic {
            width: 40px;
            height : 40px;
        }

        .universal-sequences-logo {
            background-color: #0a0a0a;
            color: #b6ccd8;
            background-color: #95308a;
            color: #b6ccd8;
        }

        .navigator-button:hover {
            background-color: white;
            color: black;
        }

        .btn-primary {
            background-color: white !important;
            color: black !important;
            border-width: 1px !important;
        }
    }

    .create-explore-learn {
        background-color: #4644446e;
        position: fixed;
        /* top: 8vw; */
        left: 77.7vw;
        display: flex;
        z-index: 4;
        width: 22.2vw;
        height: 11.1vw;

        a.selected {
            border-bottom: 1px solid #edecff;
        }

        a {
            margin: 10px;
            font-weight: 350;
            color: gray;
            font-size: 20pt;
            cursor: pointer;
            margin-bottom: 2vw;
            margin-top: auto;
        }
    }

    .logos {
        display: flex;
        position: fixed;
        width: 100%;
        background-color: #101010ba;
        height: 11.1vw;
        z-index: 2;

        .circle-logo {
            cursor: pointer;
            position: fixed;
            top: 1.5vw;
            left: 1.5vw;
            width: 8vw;
            display: none;
            z-index: 2;
        }

        .text-logo {
            cursor: pointer;
            height: 6vw;
            margin-top: 3vw;
            margin-left: auto;
            margin-right: auto;
        }
    }

    .content {
        clear: both;
        height: 100%;
        color: white;
        //margin-left: auto;
        // margin-right: auto;
        background-color: #01000c29;
        z-index: 1;
        flex: 1 1;
        flex: 1;
        //overflow-x: hidden;

        .landing-about {
            .wavy {
                animation: wavyAnim 10s 2s infinite;
                animation-direction: alternate;
                animation-timing-function: linear;
            }
        }
        .landing-about.playing {
            .wavy {
                animation: none;
            }
        }

        .landing-about {
            margin-top: 160px;

            .wavy {
                width: 100%;
                height: 10%;
                margin-top: 30px;
                margin-bottom: 60px;
                /*
                animation: wavyAnim 10s 2s infinite;
                animation-direction: alternate;
                animation-timing-function: linear;
                */
            }

            .grids-container {
                
                display: flex;
                margin-top: 20px;
                margin-left: 20%;
                margin-right: 20%;
                z-index: 2;

                .grids {
                    margin-right: 10%;

                    .grid-row {
                        display: flex;
                        .grid.pressed {
                            animation-name: gridPress;
                            animation-duration: 0.85s;
                            background-color: blue;
                        }
                        
                        .grid.playing {
                            background-color: blue;
                        }

                        .grid {
                            width: 80px;
                            height: 80px;
                            background-color: white;
                            margin: 15px;
                            cursor: pointer;
                        }
                    }
                }
            }
        }


        .pause-button {
            width: 100px;
            height: 100px;
            border-right: 30px solid white;
            border-left: 30px solid white;
            cursor: pointer;
            position: fixed;
            right: 70px;
            bottom: 300px;
        }

        .landing-navigator {
            padding: 15px;
            display: flex;
            border-bottom: 10px solid black;

            .option:hover {
                background-color: #ffffff04;
                color: black;
            }

            .option {
                cursor: pointer;
                margin-right: 20px;
                font-size: 14pt;
            }

            .option.selected {
                border-bottom: solid 2px white;
            }
        }

        .bottom-space {
            height: 120px;
        }

        .market-row {
            display: flex;
            margin-left: 10%;

            .market-whisper {
                width: 30%;
                font-size: 16pt;
                margin-left: 10%;
            }
        }

        .market-speak {
            font-size: 24pt;
            margin-left: 10%;
            width: 100%;
        }

        .horizontal-divider {
            width: 100%;
            height: 10px;
            background-color: black;
       }


        .steps-visualizer {
            display: flex;
            margin-bottom: 30px;
            margin-top: 50px;
            margin-left: auto;
            width: 50%;
            margin-right: auto;
            
        }

        .graphic {
            margin-left: 12%;
            position: relative;
            height: 135px;
            min-height: 135px;
            max-width: 40%;
            padding: 20px;
            padding-top: 35px;
            //background-color: #fffefef7;
            font-size: 12pt;
            color: white;

            .work-title {
                position: absolute;
                left: 10px;
                width: 300px;
                top: 5px;
                white-space: nowrap;
                font-size: 10pt;
                color: black;
                overflow: hidden;
                animation-name: titleFade;
                animation-duration: 1s;
                background-color: white;
                padding-left: 10px;

                .work-author {
                    color: #001eff;
                }
            }

            .bpm {
                display: none;
            }

            .track-graphic {
                height: 7px;
                
                .step-graphic {
                    width: 16px;
                    height: 5px;
                }
            }
        }
    }

    .getting-started {
        display: flex;
        flex-direction: column;

        .main-text {
            font-size: 16pt;
            margin-bottom: 20px;
        }
        
        .onboarding-step.showing-how-to {
            width: 600px;
        }

        .onboarding-step:hover {
            background-color: #191b2d
        }
        
        .onboarding-step {
            border: solid 4px #17151d;
            display: flex;
            margin: 5px;
            font-size: 13pt;
            width: 500px;
            background-color: #171821;
            cursor: pointer;

            .step-checkmark {
                width: 60px;
                height: 60px;
                background-color: #5ec3ac;
                border: 4px solid #111e1b;
            }

            .step-info {
                padding: 15px;
            }

            .how-to {
                font-size: 12pt;

                .how-to-step {
                    margin: 10px;
                }
            }
        }

        //min-height: 1000px;
    }

    .user-name-input {
        background-color: black;
        color: white;
    }

    .checkmark {
        display: inline-block;
        width: 40px;
        height: 50px;
        transform: rotate(45deg);
    }
    
    .checkmark_stem {
        position: absolute;
        width: 6px;
        height: 20px;
        background-color: #171821;;
        left: 19px;
        top: 8px;
    }
    
    .checkmark_kick {
        position: absolute;
        width: 6px;
        height: 6px;
        background-color: #171821;;
        left: 14px;
        top: 22px;
    }
}

.contact-us {
    font-size: 16pt;
    cursor: pointer;
    padding-top: 100px;
    padding-bottom: 100px;
    text-align: center;
}

.contact {
    display: flex;
    width: 100%;
    margin-top: 200px;

    .contact-info {
        margin-left: 5px;
        padding: 15px;
        background-color: #00000036;
        width: 50%;

        .name {
            font-size: 15pt;
        }

        .job-title {
            font-size: 13pt;
            margin-bottom: 10px;
        }
        
    }
}

.coke-machine-glow.part-1 {
    opacity: 0.4;
    animation-name: glowFade;
    animation-duration: 2s;
}

.coke-machine-glow {
    position: fixed;
    pointer-events: none;
    z-index: -1;
    top: 50%;
    left: 50%;
    transform: translate(-50%, calc(-50% + 80px));
    width: 75vmin;
    height: 75vmin;
    //background: radial-gradient(75.29% 75.29% at 64.96% 24.36%, #2517ff 0.52%, #1f10fb 18.75%, #298bde 34.38%, #1facff 49.48%, #889ff3 74.48%, #56adff 100%);
    background: radial-gradient(92% 75.29% at 64.96% 24.36%, #1b6ca796 0.52%, #4c100a 35.75%, #000000 34.38%, #1facff 49.48%, #194bff 74.48%, #2896ff 100%);
    filter: blur(8vmin);
    border-radius: 100%;
    visibility: visible;
    opacity: 0.3;
    z-index: 1;
    transition: all 400ms ease 0s;
}

@keyframes gridPress {
    from {background-color: white}
    to {background-color: blue}
}

@keyframes wavyAnim {
    from {transfrom: scaleY(1)}
    to {transform: scaleY(0.3)}
}

@keyframes titleFade {
    from {
        width: 0px;
        background-color: #0068ff;
    }
    to {
        width: 300px;
        background-color: white;
    }
}

@keyframes glowFade {
    from {opacity: 0.3}
    to {opacity: 0.4}
}


@keyframes graphicFade {
    from {opacity: 0}
    to {opacity: 1}
}



.logo-hiero.toggled {
    filter: invert(1);
}

.logo-hiero {
    cursor: pointer;
    position: fixed;
