$landingBlue: #4154FF;

.landing-page.part-2 {
    .logos {
        animation: fadeheader 1s ease-in-out 0s forwards;
    }
}

.landing-page.part-3 {
    .triangles {
        .triangle-row {
            .triangle {
                animation: fadetriangle2 1.5s ease-in-out 0s forwards;
            }
        }
    }
}

.landing-page.part-4 {
    animation: fadetowhite 1.5s ease-in-out 0s forwards;

    .triangles {
        .triangle-row {
            .triangle {
                animation: fadetriangle3 1.5s ease-in-out 0s forwards;
            }
        }
    }

}

.landing-page.part-3, .landing-page.part-4, .landing-page.part-5 {
    .logos {
        animation: unset;
        opacity: 0;
    }

    .create-explore-learn {
        animation: fadeexplore 1s ease-in-out 0s forwards;
    }
}

.landing-page.part-2 {
    .triangles {
        .triangle-row {
            .triangle {
                animation: fadetriangle 1.5s ease-in-out 0s forwards;
            }
        }
    }
}

.landing-page {

    .workflows-container {
        margin-left: auto;
        margin-right: auto;
        width: 50%;

        .workflow-2 {
            width:100%;
        }
    }
    
    .workflows {
        display: flex;
        align-items: flex-start;
        margin-top: 6.03vw;
        
        img.workflow-1 {
            align-items: flex-start;
            width: 500px;
            margin-left: 6.06vw;
        }
    }

    .logos {
        animation: fadeinheader 4s ease-in-out 0s forwards;
    }

    .landing-part.part-5 {
        background-color: black;
        position: relative;
        padding-top: 6.06vw;

        .overlay-text {
            position: absolute;
            top: 40%;
            width: 100vw;
            text-align: center;
            font-size: 6vw;
        }

        .l-row {
            display: flex;
            margin-left: 30%;
            margin-right: auto;

            img {
                margin: 15px;
                width: 8.08vw;
            }
        }
    }

    .text-box {
        padding: 1vw;
        font-size: 16pt;
        border: 1px solid black;
        width: 22.22vw;
        margin: 3.03vw;
    }


    .landing-part.part-3 {
        background-color: #ffffffd1;
        color: $landingBlue;

        .text-box {
            border-color: $landingBlue;
        }

        .blocks {
            width: 22.22vw;
        }

        .big-text {
            font-size: 24pt;
            margin: 3.03vw;
            img {
                margin-top: 3.03vw;
            }
        }

        .l-row {
            display: flex;
        }
    }

    .landing-part.part-4 {
        background-color: #ff5a1aeb;
        color: black;

        .crypto-row {
            display: flex;

            .diagrams {
                margin-left: 8.08vw;
                font-size: 20px;
                .box-1 {
                    background-color: #ff474785;
                    text-align: center;
                    border: 1px dashed black;
                    width: 120px;
                    height: 120px;
                    margin-bottom: 3.03vw;
                }

                .box-3 {
                    width: 64px;
                    margin-left: 25px;
                    height: 64px;
                    background-image: url(/sp404.svg);
                    background-size: 100% 100%;
                }

                .box-2 {
                    margin-bottom: 3.03vw;
                    position: relative;
                    margin-left: 16px;
                    text-align: center;
                    border: 1px dashed black;
                    width: 86px;
                    height: 86px;
                    background-color: #ff474785;

                    .coming-soon {
                        position: absolute;
                        bottom: 0px;
                        text-align: center;
                        font-size: 8pt;
                        width: 100%;
                    }
                }
            }
        }

        .more-crypto {
            margin-left: 8.08vw;
            margin-top: 3.03vw;
            font-size: 21pt;
            padding-bottom: 8.08vw;
        }

        .open-source {
            font-size: 33px;
            background-color: #ff4a0b52;
            height: 100%;
            padding-top: 20px;
            padding-left: 25px;
            position: relative;

            .coming-soon-open {
                position: absolute;
                top: 65px;
                font-size: 8pt;
            }

            .open-protocol {
                position: absolute;
                bottom: 20px;
                right: 25px;
            }

            span {
                z-index: 4;
                position: absolute;
            }

            .diagonal {
                z-index: 2;
                width: 0;
                height: 0;
                filter: blur(25px);
                border-left: 369px solid #ff227447;
                border-bottom: 369px solid transparent;
                position: absolute;
                bottom: 0px;
                left: 0px;
            }
        }

        .on-chain {
            padding: 8px;
            font-size: 22px;
        }

        .splits {
            padding-top: 10%;
            padding-left: 5%;
            background-color: #ff474785;
            .coming-soon {
                bottom: 3px;
                left: 8px;
            }
        }
        .splits, .nft {
            font-size: 33px;
            position: relative;
            width: 100%;
            height: 100%;
            .coming-soon {
                position: absolute;
                text-align: center;
                font-size: 8pt;
            }
        }

        .nft {
            position: relative;

            .coming-soon {
                bottom: -3px;
            }

            .nft-circle {
                opacity: .8;
    background: radial-gradient(92% 75.29% at 64.96% 24.36%, #ff5caf96 0.52%, #ff3e6a9e 35.75%, #000000 34.38%, #ff194e 49.48%, #ff19b8 74.48%, #2896ff 100%);
                filter: blur(40px);
                position: absolute;
                border-radius: 100%;
                top: 0px;
                left: 0px;
                /* transform: translate(-50%, calc(-50% + 80px)); */
                width: 100%;
                height: 100%;
            }
        }

        .sp404 {
            background-image: url(/sp404.svg);
            height: 100%;
            width: 100%;
            background-size: cover;
        }
        
        .nft {
            padding-top: 20%;
            line-height: 30px;
            padding-left: 25%;
        }

        .landing-column {
            width: 90%;
            font-size: 24pt;
            margin-left: 6.06vw;
            padding-bottom: 100px;
        }

        .sp-404s {
            margin-top: 30px;
            width: 33.33vw;
            margin-bottom: 30px;
        }

    }

    .landing-part.part-3, .landing-part.part-4 {
        .mission-subheader.left {
            text-align: unset;
            margin-left: 6.06vw;
            padding-top: 3.03vw;
        }
        
        .mission-subheader {
            margin-top: unset;
            padding-bottom: 3.03vw;
        }

        .landing-columns {
            display: flex;
            font-size: 20pt;

            .landing-column.right {
                
                margin-left: 3.03vw;
                margin-top: 11.11vw;
                p {
                    background-color: #ffffff2e;
                    padding: 10px;
                }
            }

            .landing-column {
                .l-block {
                    background-color: #1fff989e;
                    background-image: linear-gradient(45deg, #a7baff94, transparent);
                    text-align: center;
                    padding: 10px;
                    margin: 30px;
                }

                .block-types {
                    display: flex;
                    .l-block {
                        border-radius: 5px;
                    }
                }
            }
        }
    }

    .landing-part.part-2 {
        margin-top: 22.2vw;
        display: flex;
        margin-left: 6.03vw;

        .landing-column {
            width: 33.3vw;
            font-size: 20pt;
            margin-right: 11.1vw;

            .fake-sequencer {
                margin-bottom: 20px;
                
                .sequencer-row {
                    display: flex;
                    .step {
                        width: 21px;
                        height: 14px;
                        margin: 3px;
                        background-color: #C4C4C4;
                        opacity: 0.2;
                    }

                    .step.on {
                        background-color: #04FFD2;
                        opacity: 1;
                    }
                }
            }
        }
    }

    .mission-subheader {
        text-align: center;
        margin-top: 3.03vw;
        font-size: 30pt;
    }
}

@keyframes fadeheader {
    to {opacity: 0;}
}

@keyframes fadeinheader {
    to {opacity: 1;}
    from {opacity: 0;}
}


@keyframes fadetriangle {
    to {border-bottom-color: #0b0b15;}
}

@keyframes fadetriangle2 {
    to {
        border-bottom-color: #25308e;
    }
}

@keyframes fadetriangle3 {
    to {
        border-bottom-color: red;
    }
}

@keyframes fadeexplore {
    to {
        background-color: #e4e1e1c7;
    }
}

@keyboard fadetowhite {
    to {
        background-color: white;
    }
}

