.doc-sidebar {
    overflow-x: hidden;
    .annotated-sidebar.midi-effects-sidebar {
        .option {
            padding-left: 10px;
        }
    }

    .annotated-sidebar {
        overflow: hidden;
        height: 100%;
        animation-name: expand-sidebar;
        animation-duration: 1s;
        margin-left: 10px;
        font-size: 12pt;
        .option {
            color: gray;
            padding-top: 3px;
            padding-bottom: 3px;
            cursor: pointer;
            display: flex;

            .tag {
                font-size: 7pt;
                margin-left: auto;
                margin-top: 5px;
            }
        }

        .option.selected, .option:hover {
            color: white;
        }
    }
}

.learn-the-editor {
    position: relative;
    margin-left: -20px;
    margin-top: 20px;

    svg.annotation-links {
        pointer-events: none;
        position: absolute;
        left: 0px;
        top: 0px;
        width: 100%;
        height: 600px;
        z-index: 100000000000000;

        polygon.visual-link {
            fill: url(#AnnotationGradient);
        }

        rect.visual-overlay {
            stroke: #94c3e5;
            fill: #94c3e51c;
        }
        
    }
    .annotated-part {
        position: relative;
        .part-name {
            font-size: 14pt;
            margin-bottom: 10px;
            border-bottom: 1px solid #ffffff2e;
        }

        .part-description {
            div {
                margin-bottom: 10px;
                line-height: 20px;
            }
        }

        .now-interact {
            margin-top: 20px;
            background-color: white;
            color: black;
            padding: 5px;
            border-radius: 15px;
            text-align: center;
        }

        .nav-buttons {
            display: flex;
            position: absolute;
            bottom: 10px;
            
            .current-number {
                margin-left: 10px;
                line-height: 30px;

                span {
                    margin-left: 2px;
                    margin-right: 2px;
                    color: gray;
                }
            }

            .arrow-container {
                width: 30px;
                height: 30px;
                background-color: #ffffff2e;
                padding: 5px;

                .disabled {
                    pointer-events: none;
                    opacity: 0.2;
                }

                .arrow-left {
                    margin-right: 10px;
                    border-top: 8px solid transparent;
                    border-bottom: 8px solid transparent;
                    border-right: 18px solid #f0f1f5;
                    margin-top: 2px;
                }

                .arrow-right {
                    border-top: 8px solid transparent;
                    border-bottom: 8px solid transparent;
                    border-left: 18px solid #f0f1f5;
                }
            }
        }

        //border-top-left-radius: 10px;
        //border-bottom-left-radius: 10px;
        position: absolute;
        width: 300px;
        height: 300px;
        padding: 20px;
        z-index: 100000000000001;
        background-color: #11132beb;
        color: #ffffff;
        backdrop-filter: blur(4px);
        border: 8px solid #465160;
    }
}
